import React from 'react'
import Waypoint from 'react-waypoint'
import scrollToComponent from 'react-scroll-to-component';
import './inspection-section.scss'
import ProductRange from '-!babel!svg-react-loader?name=Icon!./mis.svg';
// import image from './PNG/manual-inspection.png'

class DeliverySection extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            sectionOneEnter: false,
            sectionTwoEnter: false,
            sectionThreeEnter: false,
        }
        this._handleWaypointEnter = this._handleWaypointEnter.bind(this);
        this._handleWaypointLeave = this._handleWaypointLeave.bind(this);
    }
    _handleWaypointEnter(){
        console.log(this.state.sectionOneEnter);
        scrollToComponent(this.view, { offset: 0, align: 'top', duration: 1000});
        this.setState({
            sectionOneEnter: true
        })
    }
    _handleWaypointLeave(){
        console.log('viewed..!');
        this.setState({
            sectionOneEnter: false
        })
    }
    render(){
        return(
        <Waypoint onEnter={this._handleWaypointEnter} onLeave={this._handleWaypointLeave}>
            <div className={"section inspection-section" + (this.state.sectionOneEnter ? " fade-in" : " fade-out")} ref={(section) => { this.view = section; }}>
                <div className="container">
                    <h1 className="section-title">Our genuine <span>Quality Control</span> process delivered 3x times more employee wellness than before, to ORACLE</h1>
                    <div className="row">
                        <div className="col-5">
                            <p className="section-lead">An expired snack at the office may cost you dear!</p>
                            <p className="section-lead">Thats why in spite of all tech-enabled quality checks, we have the final manual inspection, to ensure safety on what employees consume, which further strengthens the employee-employer relationship.</p>
                        </div>
                        <div className="col-10">
                            <ProductRange />
                        </div>
                    </div>
                </div>
            </div>
        </Waypoint>
        )
    }
}

export default DeliverySection;


