import React from 'react'
import Waypoint from 'react-waypoint'
import scrollToComponent from 'react-scroll-to-component';
import './customer-service-section.scss'
import ProductRange from '-!babel!svg-react-loader?name=Icon!./cs.svg';
// import image from './PNG/customer-services.png'
class DeliverySection extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            sectionOneEnter: false,
            sectionTwoEnter: false,
            sectionThreeEnter: false,
        }
        this._handleWaypointEnter = this._handleWaypointEnter.bind(this);
        this._handleWaypointLeave = this._handleWaypointLeave.bind(this);
    }
    _handleWaypointEnter(){
        console.log(this.state.sectionOneEnter);
        scrollToComponent(this.view, { offset: 0, align: 'top', duration: 1000});
        this.setState({
            sectionOneEnter: true
        })
    }
    _handleWaypointLeave(){
        console.log('viewed..!');
        this.setState({
            sectionOneEnter: false
        })
    }
    render(){
        return(
        <Waypoint onEnter={this._handleWaypointEnter} onLeave={this._handleWaypointLeave}>
            <div className={"section customer-services-section" + (this.state.sectionOneEnter ? " fade-in" : " fade-out")} ref={(section) => { this.view = section; }}>
                <div className="container">
                    <h1 className="section-title">Our dedicated <span>Relationship Manager </span> works behind-the-scenes to solve unpredictable problems for ALTISOURCE</h1>
                    <div className="row">
                        <div className="col-10">
                            <ProductRange/>
                        </div>
                        <div className="col-5">
                            <p className="section-lead">Procurement can be a thankless, daunting and demanding job. That is why our relationship managers (read sourcing specialists) work behind-the-scenes to solve unpredictable problems and strive to make procurement personalised and delightful experience.</p>
                        </div>
                    </div>
                </div>
            </div>
        </Waypoint>
        )
    }
}

export default DeliverySection;


