import React from 'react'
// import './hero-section.scss'
import Waypoint from 'react-waypoint'
import scrollToComponent from 'react-scroll-to-component';
import './strategic-section.scss'
import StrategicSourcing from '-!babel!svg-react-loader?name=StrategicSourcing!./StrategicSourcing_1.svg';
class HeroSection extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isHeroActive: false
        }
        this._handleWaypointEnter = this._handleWaypointEnter.bind(this);
        this._handleWaypointLeave = this._handleWaypointLeave.bind(this);
    }
    _handleWaypointEnter(){
        console.log("viewing..!");
        scrollToComponent(this.view, { offset: 0, align: 'top', duration: 1000});
        this.setState({
            isHeroActive: true
        })
    }
    _handleWaypointLeave(){
        console.log('viewed..!');
        this.setState({
            isHeroActive: false
        })
    }
    render(){
        return(
        <Waypoint onEnter={this._handleWaypointEnter} onLeave={this._handleWaypointLeave}>
            <div className="section strategic-section" ref={(section) => { this.view = section; }}>
                <div className={"container" + (this.state.isHeroActive ? " fade-in" : " fade-out")}>
                    <h1 className="section-title">REVTECH eliminated inconsistency in their supplies by purchasing almost 85% of <span>all Office Supplies</span> from us</h1>
                    <StrategicSourcing className="hero-image"/>
                    <p className="quote">Businesses receive all the office supplies throughout the year;  from stationery to the pantry eatables, high-end electronics to cleaning products through us. </p>
                </div>
            </div>
        </Waypoint>
        )
    }
}

export default HeroSection;


