import React from 'react'
import HeroSection from './home/strategic-section'
import ProductRangeSection from './home/product-range-section'
import CareerSection from './home/career-section'
import DeliverySection from './home/delivery-section'
import InspectionSection from './home/inspection-section'
import CustomerServiceSection from './home/customer-service-section'
import IntelligentFlow from './home/intelligent-section'
import Eprocurement from './home/e-procurement-section'
import ReportsSection from './home/report-section'

const IndexPage = () => (
  <div className="wrapper">
    <HeroSection />
    <ProductRangeSection />
    <DeliverySection />
    <InspectionSection />
    <CustomerServiceSection />
    <IntelligentFlow />
    <ReportsSection/>
    <Eprocurement />
    {/* <CareerSection /> */}
  </div>
)

export default IndexPage


